#certificatePage{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 1.75rem auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.certificatePageTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}

.certificateDesc{
    font-weight: 300;
    font-size: 1rem;
}

.certificateImgs{
    margin:1rem 0;
}

.certificateImg{
    object-fit: cover;
    width: 100%;
    min-width: 9rem;
    margin:auto;
    padding: 0.25rem;
}

#certificates{
    padding:2rem;
}