#portfolio{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin:1.75rem auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
    text-align: center;
    padding: 0 1rem;
}

.worksTitle{
    margin:1rem 0;
    font-style: 3rem;
}

.workDesc{
    text-align: left;
    font-weight: 300;
    font-size: 1rem; 
    max-width: 45rem;
    margin-bottom: 10px;
} 

.card-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 60vw;
    max-width: 65rem;  
    gap:10px;
}