@keyframes slideIn {
    0% {
      transform: translateX(-100%);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }
.card{
    background-color:white;
    display: flex;
    flex-direction: column;
    object-fit: cover;
    height: 20rem;
    width: 15rem;
    margin:0.5rem;
    background: linear-gradient(135deg, #2c2c2c 0%, #3e3e3e 20%, #b8860b 80%, #2c2c2c 100%);
    color: #fff; /* Text color for contrast */
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
    overflow: hidden;
    opacity: 0; /* Start hidden */
    transform: translateY(20px);

}
.card.animated {
    animation: slideIn 1s ease-in-out forwards;
}


/* Optional: Add a hover effect */
.card:hover {
    transform:scale(1.05);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4), 0 4px 8px rgba(208, 181, 8, 0.5); /* Enhances brightness on hover */
    background: linear-gradient(135deg, #101010 0%, #dfb419 100%); /* Bright black to rich gold gradient */
    border:1px solid #bfa305;
}

.card-heading{
    font-size: 1.5rem;
    font-weight: 600;
    color:#FFD700;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    padding-bottom: 0.2rem;
}

.card-img{
    height: 10rem;
    margin: 0.4rem 0;
    border-radius: 0.7rem;
    transition: opacity 0.3s ease;
}

.card:hover .card-img {
    opacity: 0.9;
}


.card-para{
    font-size: 0.8rem;
    font-weight: normal;
    color:#fff;

}

.project-link{
    font-size: 0.7rem;
    font-weight: 700;
    color: black;
    margin-top: 0.6rem;
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s ease;
    justify-items: end;
} 

.project-link:hover {
    color: #0056b3;
    
}